import s from "./StyleLogin/Login.module.css"
import React from 'react';
import {useDispatch, useSelector} from "react-redux";
import {actions, login} from "../../Redux/auth-reducer";
import {Redirect} from "react-router-dom";
import {AppStateType} from "../../Redux/redux-store";
import {ReduxLoginForm} from "./LoginForm";


export type LogingFormValuesStyle = {
    email: string,
    password: string,
    rememberme: boolean,
    captcha: string
}

export const Login: React.FC = (props) => {


    const dispatch = useDispatch()


    const captchaUrl = useSelector((state: AppStateType) => state.auth.captchaUrl)
    const isAuth = useSelector((state: AppStateType) => state.auth.isAuth)
    const watchstatus = useSelector((state: AppStateType) => state.auth.watchstatus)
    const icon_watch = useSelector((state: AppStateType) => state.auth.icon_watch)

    const onSubmit = (formData: LogingFormValuesStyle) => {
        dispatch(login(formData.email, formData.password, formData.rememberme, formData.captcha))
    }

    const watchStatus = (text: string) => {
        dispatch(actions.watchStatus(text))
    }
    const watchIconStatus = (text: string) => {
        dispatch(actions.watchIconStatus(text))
    }


    if (isAuth) {
        return <Redirect to={'/profile'}/>
    } else {
    }

    return (
        <div className={s.content}>
            <div className={s.login_block}>
                <h1>Login</h1>
                <ReduxLoginForm onSubmit={onSubmit} watchstatus={watchstatus}
                                watchStatus={watchStatus} icon_watch={icon_watch}
                                watchIconStatus={watchIconStatus} captchaUrl={captchaUrl}/>
            </div>

        </div>
    )

}


