import {PhotosType, ProfileType} from "../Types/Types";
import {instance, APIResponseType, ResultCodeEnum} from "./API";


export const profileAPI = {
    getProfile(userId: number) {
        return instance.get<ProfileType>(`profile/` + userId).then(response => {
            return response.data
        })


    },
    getStatus(userId: number) {
        return instance.get<string>(`profile/status/` + userId).then(response => {
            return response.data

        })
    },


    updateStatus(status: string) {
        return instance.put<APIResponseType<string>>(`profile/status/`, {status: status}).then(response => {
            return response.data

        })
    },
    savePhoto(file: File) {
        const formData = new FormData();
        formData.append("image", file)
        return instance.put<APIResponseType<PhotosType>>(`profile/photo`, formData, {

            headers: {
                'Content-Type': 'multipart/form-data '
            }
        }).then(response => {
            return response.data

        })
    },


    saveProfile(profile: ProfileType) {
        return instance.put<APIResponseType<ProfileType>>(`profile/`, profile).then(response => {
            return response.data

        })
    }
}