import {instance, APIResponseType} from "./API";


type getAuthType = {
    id: number
    email: string
    login: string

}

type loginType = {
    userId: number
}


export const authAPI = {
    getAuth() {
        return instance.get<APIResponseType<getAuthType>>(`auth/me`).then(res => res.data)
    },
    login(email: string, password: string, rememberme = false, captcha: null | string = null) {
        return instance.post<APIResponseType<loginType>>(`auth/login`, {
            email,
            password,
            rememberme,
            captcha
        }).then(res => res.data)
    },

    logout() {
        return instance.delete(`auth/login`,)
    }
}