import userPhoto from "../../assets/img/user.png"
import s from './StyleUsers/Users.module.css';
import React from "react";
import {NavLink} from "react-router-dom";
import FollowPreloader from "../common/Followpreloader/FolloPreloader";
import {UserType} from "../../Types/Types";


type Props = {
    user: UserType
    isReloadButton: Array<number>
    folowSuccess: (userId: number) => void,
    unfolowSuccess: (userId: number) => void,

}

let User: React.FC<Props> = ({user, isReloadButton, folowSuccess, unfolowSuccess}) => {

    let unfollow = "Unfollow" as any,
        follow = "Follow" as any

    if (isReloadButton.some(id => id == user.id)) {
        unfollow = <FollowPreloader/>
    }

    if (isReloadButton.some(id => id == user.id)) {
        follow = <FollowPreloader/>
    }

    return (
        <div>
            <div className={s.all}>
                <div className={s.follow}>
                    <NavLink to={'/profile/' + user.id}> <img
                        src={user.photos.small != null ? user.photos.small : userPhoto}
                        className={s.img}/></NavLink>
                    {user.followed
                        ? <button disabled={isReloadButton.some(id => id == user.id)} className={s.buttonunfolowed}
                                  onClick={(e) => {
                                      unfolowSuccess(user.id)
                                  }
                                  }> {unfollow} </button>
                        : <button disabled={isReloadButton.some(id => id == user.id)} className={s.buttonfolowed}
                                  onClick={(e) => {
                                      folowSuccess(user.id)
                                  }}>{follow} </button>
                    }
                </div>
                <div className={s.prof}>
                    <div className={s.info}>
                        <div className={s.name}>{user.name}</div>
                        <div className={s.status}>{user.status}</div>
                    </div>
                    <div className={s.location}>
                        <div className={s.city}>{"users.location.city"}</div>
                        <div className={s.country}>{"users.location.country"}</div>
                    </div>
                </div>

            </div>

        </div>
    )
}

export default User