import React from "react";
import {Redirect} from "react-router-dom";
import {connect} from "react-redux";
import {AppStateType} from "../Redux/redux-store";

let mapStateTopropsForRedirect = (state: AppStateType) => ({
    isAuth: state.auth.isAuth,
} as MapPropsType)

type MapPropsType = {
    isAuth: boolean
}


type MapDispatchType = {}

export function withAuthRederect<COM>(Component: React.ComponentType<COM>) {
    const RedirectComponen: React.FC<MapDispatchType & MapPropsType> = (props) => {

        let {isAuth, ...restProps} = props
        if (!isAuth) return <Redirect to={"/login"}/>
        return <Component {...restProps as COM}/>
    }


    let ConectAuthRedirectComponent = connect<MapPropsType, MapDispatchType, COM, AppStateType>(
        mapStateTopropsForRedirect,
        {})(RedirectComponen)

    return ConectAuthRedirectComponent

}