import {ResultCodeEnum} from "../API/API";
import {stopSubmit} from "redux-form";
import {PhotosType, PostType, ProfileType} from "../Types/Types";
import {ThunkAction} from "redux-thunk";
import {actionsType, AppStateType, BaseThunkType} from "./redux-store";
import {profileAPI} from "../API/ProfileAPI";


let initialState = {
    post: [] as Array<PostType>,
    postStateText: "",
    profile: null as ProfileType | null,
    reloader: false,
    status: "",
    newstatusreloader: false,


};

export type initialStateType = typeof initialState

const profileReducer = (state = initialState, action: ActionsTypes): initialStateType => {

    switch (action.type) {

        case "SN/PROFILE-REDUCER-ADD_POST": {
            let newPost
            let n
            n = state.post.length - 1 + 1;
            newPost = {
                id: n,
                message: action.newPost,
                like: 0,
            }
            return {
                ...state,
                post: [...state.post, newPost],

            }


        }


        case "SN/PROFILE-REDUCER-SET_USER_PROFILE": {
            return {...state, profile: action.profile}
        }
        case "SN/PROFILE-REDUCER-Reload": {
            return {...state, reloader: action.reloader}
        }

        case "SN/PROFILE-REDUCER-SET_STATUS_PROFILE": {
            return {...state, status: action.status}
        }
        case "SN/PROFILE-REDUCER-NEW_STATuS_RELOADER": {
            return {...state, newstatusreloader: action.newstatusreloader}
        }
        case "SN/PROFILE-REDUCER-DELETE_POST": {
            return {...state, post: state.post.filter(p => p.id != action.postId)}
        }
        case "SN/PROFILE-REDUCER-SAVE_PHOTO": {

            return {...state, profile: {...state.profile, photos: action.photos} as ProfileType}
        }

        default:
            return state;


    }


}

type ActionsTypes = actionsType<typeof actions>


export const actions = {
    addPost: (newPost: string) => {
        return {
            type: "SN/PROFILE-REDUCER-ADD_POST",
            newPost,
        } as const
    },
    deletePost: (postId: number) => {
        return {
            type: "SN/PROFILE-REDUCER-DELETE_POST",
            postId,
        } as const
    },

    savePhotosSucsess: (photos: PhotosType) => {
        return {
            type: "SN/PROFILE-REDUCER-SAVE_PHOTO",
            photos,
        } as const
    },
    setUserProfile: (profile: ProfileType) => {
        return {
            type: "SN/PROFILE-REDUCER-SET_USER_PROFILE",
            profile,
        } as const
    },
    setStatusProfile: (status: string) => {
        return {
            type: "SN/PROFILE-REDUCER-SET_STATUS_PROFILE",
            status,
        } as const
    },

    isReader: (reloader: boolean) => {
        return {
            type: "SN/PROFILE-REDUCER-Reload",
            reloader,
        } as const
    },
    isStatusReload: (newstatusreloader: boolean) => {
        return {
            type: "SN/PROFILE-REDUCER-NEW_STATuS_RELOADER",
            newstatusreloader,
        } as const
    }

}

type ThunkType = BaseThunkType<ActionsTypes>

export const getProfile = (userId: number): ThunkType => {
    return async (dispatch) => {
        dispatch(actions.isReader(true))

        let data = await profileAPI.getProfile(userId)
        dispatch(actions.setUserProfile(data))
        dispatch(actions.isReader(false))


    }
}


export const getProfileStatus = (userId: number): ThunkType => {
    return async (dispatch) => {
        dispatch(actions.isStatusReload(true))

        let data = await profileAPI.getStatus(userId)
        dispatch(actions.setStatusProfile(data))

        dispatch(actions.isStatusReload(false))

    }
}

export const updateProfileStatus = (status: string): ThunkType => {
    return async (dispatch) => {


        let data = await profileAPI.updateStatus(status)
        if (data.resultCode === ResultCodeEnum.Success) {
            dispatch(actions.setStatusProfile(status))
        }
    }

}
export const savePhoto = (file: File): ThunkType => {
    return async (dispatch) => {
        let data = await profileAPI.savePhoto(file)
        if (data.resultCode === 0) {
            dispatch(actions.savePhotosSucsess(data.data))
        }
    }
}


export const saveProfile = (profile: ProfileType): ThunkType => {
    return async (dispatch: any, getState: any) => {
        const userId = getState().auth.id
        let data = await profileAPI.saveProfile(profile)
        if (data.resultCode === 0) {
            dispatch(getProfile(userId))
        } else {
            let message = data.messages.length > 0 ? data.messages[0] : "Some error";
            dispatch(stopSubmit("edit-profile", {_error: message}))
            return Promise.reject(message)
        }
    }
}


export default profileReducer;