import React from "react";
import {Field, InjectedFormProps, reduxForm} from "redux-form";
import view from "../../assets/img/view.svg";
import noview from "../../assets/img/no-view.svg";
import s from "./StyleLogin/Login.module.css";
import {createField, Input} from "../common/FormsControl/FormContorl";
import {maxLenghtCrater, required} from "../../Utils/Validators/validator";
import {LogingFormValuesStyle} from "./Login";

const maxLengh = maxLenghtCrater(100)


type LoginFormOwnProps = {
    watchstatus: string | null,
    watchIconStatus: (text: string) => void,
    watchStatus: (text: string) => void,
    captchaUrl: string | null,
    icon_watch: string
}

type LogingFormValuesStyleKey = Extract<keyof LogingFormValuesStyle, string>


export const LoginForm: React.FC<InjectedFormProps<LogingFormValuesStyle, LoginFormOwnProps> & LoginFormOwnProps> = (props) => {


    let onWatch = () => {
        if (props.watchstatus == "password") {
            props.watchStatus("")
            props.watchIconStatus(view)

        } else {
            props.watchStatus("password")
            props.watchIconStatus(noview)

        }
    }

    return (

        <form onSubmit={props.handleSubmit} className={s.form_login}>
            <div>
                <Field className={s.inputform} component={Input} placeholder={"Login"} name={"email"}
                       validate={[required, maxLengh]}/>
            </div>

            <div className={s.password}>
                <Field className={s.inputform_password} component={Input} placeholder={"Password"}
                       type={props.watchstatus}
                       name={"password"}
                       validate={[required, maxLengh]}/>

                <span onClick={onWatch} className={s.wathc_password}> <img  src={props.icon_watch} alt="" height="20"
                                                                           width="20"/></span>


            </div>

            {props.captchaUrl && <img src={props.captchaUrl}/>}
            {props.captchaUrl && createField<LogingFormValuesStyleKey>("Antibot", "captcha", [required], Input)}

            {props.error &&
            <div className={s.no_login_error}> {props.error}</div>


            }
            <div className={s.check}>

                <Field component={Input} type={"checkbox"} name={"rememberme"} className={s.checkbox}/>
                <span>remember me</span>
            </div>


            <div>
                <button className={s.button}>Login</button>
            </div>
        </form>
    )
}

export const ReduxLoginForm = reduxForm<LogingFormValuesStyle, LoginFormOwnProps>({
    form: 'login'

})
(LoginForm)