import s from './StyleFormControl/FormControl.module.css';
import {Field, WrappedFieldMetaProps, WrappedFieldProps} from "redux-form";
import {FieldValidatorType} from "../../../Utils/Validators/validator";
import React from "react";

type FromControlType = {
    meta: WrappedFieldMetaProps,
    children: React.ReactNode
}

const FromControl: React.FC<FromControlType> = ({meta: {touched, error}, children}) => {


    let showing
    if (touched && error) {
        showing = s.error
    } else if (touched) {
        showing = s.touch
    } else if (error) {
        showing = s.error

    } else {
        showing = s.standart
    }

    return (

        <div className={s.formControl + " " + showing}>
            <div>
                {children}
            </div>

        </div>
    )

}


export const TextArea: React.FC<WrappedFieldProps> = (props) => {
    const {input, meta, ...restProps} = props

    const showError = props.meta.touched && props.meta.error

    return <FromControl {...props} > <textarea {...props.input} {...restProps}/>
        {showError && <div className={s.errormessagearea}>{meta.error}</div>}</FromControl>

}

export const Input: React.FC<WrappedFieldProps> = (props) => {
    const {input, meta, ...restProps} = props

    const showError = props.meta.touched && props.meta.error

    return <FromControl {...props}> <input {...props.input} {...restProps}/>
        {showError && <div className={s.errormessageinput}>{meta.error}</div>}</FromControl>


}


export function createField<FormCaseType extends string>(placeholder: string | undefined,
                                                         name: FormCaseType,
                                                         validators: Array<FieldValidatorType>,
                                                         componen: React.FC<WrappedFieldProps>,
                                                         props = {}, text = "") {
    return (
        <div>
            <Field placeholder={placeholder} name={name}
                   validate={validators}
                   component={componen}
                   {...props}/> {text}
        </div>
    )
}