import s from './StyleUsers/Users.module.css';
import React, {useEffect} from "react";
import User from "./User";
import {UserSearchForm} from "./UserSearchForm";
import {
    FilterType, getUser, folowSuccess, unfolowSuccess
} from "../../Redux/users-reducer";


import {useDispatch, useSelector} from "react-redux";
import {
    getCurrentPage, getIsReloadButton,
    getPageSize,
    getTotalUserConunt,
    getUserFilter,
    getUsersSuper
} from "../../Redux/users-selector";
import {withAuthRederect} from "../../hoc/withAuthRederect";
import {useHistory} from "react-router-dom";
import * as querystring from "querystring"
import Pagination from "./Pagenation";


type QueryParamsType = { term?: string; page?: string; friend?: string };
export const Users: React.FC = withAuthRederect(() => {

        const dispatch = useDispatch()

        const history = useHistory()

        const users = useSelector(getUsersSuper)
        const totalUserConunt = useSelector(getTotalUserConunt)
        const pageSize = useSelector(getPageSize)
        const currentPage = useSelector(getCurrentPage)
        const filter = useSelector(getUserFilter)
        const isReloadButton = useSelector(getIsReloadButton)

        useEffect(() => {
            const parsed = querystring.parse(history.location.search.substring(1)) as QueryParamsType

            let actualPage = currentPage
            let actualFilter = filter


            if (!!parsed.page) actualPage = +parsed.page


            if (!!parsed.term) actualFilter = {...actualFilter, term: parsed.term as string}

            if (!!parsed.friend) actualFilter = {
                ...actualFilter,
                friend: parsed.friend === "null" ? null : parsed.friend === "true" ? true : false
            }
            dispatch(getUser(actualPage, pageSize, actualFilter))


        }, [])


        useEffect(() => {
            let query: QueryParamsType = {}

            if (!!filter.term) query.term = filter.term
            if (filter.friend !== null) query.friend = String(filter.friend)
            if (currentPage !== 1) query.page = String(currentPage)


            history.push({
                pathname: "/users",
                search: querystring.stringify(query)
            })
        }, [filter, currentPage])


        const onPageChanged = (pageNumber: number) => {
            dispatch(getUser(pageNumber, pageSize, filter))
        }


        const onFilterChanged = (filter: FilterType) => {
            dispatch(getUser(1, pageSize, filter))
        }


        const folow = (userId: number) => {
            dispatch(folowSuccess(userId))
        }
        const unfolow = (userId: number) => {
            dispatch(unfolowSuccess(userId))

        }


        let U = users.map(u => <User user={u} key={u.id} isReloadButton={isReloadButton}
                                     folowSuccess={folow} unfolowSuccess={unfolow}/>)


        return (
            <div className={s.content}>
                <UserSearchForm onFilterChanged={onFilterChanged}/>

                <Pagination currentPage={currentPage} onPageChanged={onPageChanged}
                            totalUserConunt={totalUserConunt} pageSize={pageSize}
                />


                {U}
            </div>
        )
    }
)
