export type initialStateType = typeof initialState

const dialogsReducer = (state = initialState, action: ActionsTypes): initialStateType => {

    switch (action.type) {

        case "SN/DIALOGS-REDUSER-SEND_MESSAGE": {
            let text = action.body

            return {
                ...state,
                message: [...state.message, {id: 10, message: text}]
            }

        }
        default:
            return state;
    }
}
type ActionsTypes = actionsType<typeof actions>


export const actions = {
    sendtext: (body: string) => {
        return {
            type: "SN/DIALOGS-REDUSER-SEND_MESSAGE",
            body,

        } as const
    }

}


export default dialogsReducer;