import React from "react";
import {Route, withRouter, Switch, NavLink} from 'react-router-dom';
import './components/StyleApp/apps.css';
import {Login} from "./components/Login/Login";
import {Component} from "react";
import {connect} from "react-redux";
import {compose} from "redux";
import {InitiolazionApp} from "./Redux/app-reducer";
import LoginLoader from "./components/common/LoginLoader/LoginLoader";
import {withSuspense} from "./hoc/Suspense";
import {AppStateType} from "./Redux/redux-store";
import {Users} from "./components/Users/Users";
import 'antd/dist/antd.css';

import {Layout, Menu, Breadcrumb} from 'antd';
import {UserOutlined} from '@ant-design/icons';
import {HeaderApp} from "./components/Header/Header";


const {SubMenu} = Menu;
const {Content, Footer, Sider} = Layout;

const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'))
const ProfileContainer = React.lazy(() => import("./components/Profile/ProfileContainet"))
const ChatPage = React.lazy(() => import("./components/Page/Chat/ChatPage"))


type MapProps = ReturnType<typeof mapStateToprops>
type DispatchProps = {
    InitiolazionApp: () => void
}


const SuspendedDialogs = withSuspense(DialogsContainer)

const SuspendedProfile = withSuspense(ProfileContainer)

const SuspendedChat = withSuspense(ChatPage)


class App extends Component<MapProps & DispatchProps> {
    componentDidMount() {
        this.props.InitiolazionApp();
    }


    render() {
        if (!this.props.initiolazion) {
            return <LoginLoader/>
        }


        return (

            <Layout>
                <HeaderApp/>
                <Content style={{padding: '0 50px'}}>

                    <Layout className="site-layout-background" style={{padding: '24px 0'}}>
                        <Sider className="site-layout-background" width={200}>
                            <Menu
                                mode="inline"
                                defaultSelectedKeys={['1']}
                                defaultOpenKeys={['sub1']}
                                style={{height: '100%'}}
                            >
                                <SubMenu key="sub1" icon={<UserOutlined/>} title="Friend Finder">
                                    <Menu.Item key="1"><NavLink to="/profile">Profile</NavLink></Menu.Item>
                                    <Menu.Item key="2"><NavLink to="/dialogs">Dialogs</NavLink></Menu.Item>
                                    <Menu.Item key="3"><NavLink to="/users">Developers</NavLink></Menu.Item>
                                    <Menu.Item key="4"><NavLink to="/chat">Chat</NavLink></Menu.Item>
                                </SubMenu>

                            </Menu>
                        </Sider>
                        <Content style={{padding: '0 24px', minHeight: 280}}>
                            <Switch>
                                <Route path="/login" render={() => <Login
                                />}/>
                                <Route path="/profile/:userId?" render={() => <SuspendedProfile/>}/>

                                <Route path="/dialogs" render={() => <SuspendedDialogs/>}/>

                                <Route path="/users" render={() => <Users
                                />}/>

                                <Route path="/chat" render={() => <SuspendedChat
                                />}/>

                                <Route path="*" render={() => <Login
                                />}/>


                            </Switch>
                        </Content>
                    </Layout>
                </Content>
                <Footer style={{textAlign: 'center'}}>SocialNetwork-2021</Footer>
            </Layout>
        )
    }
}

let mapStateToprops = (state: AppStateType) => {
    return {
        initiolazion: state.appPage.initiolazion,
    }


}

export default compose(
    withRouter,
    connect(mapStateToprops,
        {
            InitiolazionApp
        }
    ),
)(App)


