import {Field, Form, Formik} from "formik";
import React from "react";
import {FilterType} from "../../Redux/users-reducer";
import {useSelector} from "react-redux";
import {getUserFilter} from "../../Redux/users-selector";
import {Button, Select} from "antd";

import s from "./StyleUsers/Users.module.css"


type Props = {
    onFilterChanged: (filter: FilterType) => void
}


const FormValidator = (values: any) => {
    const errors = {};

    return errors;
}


export const UserSearchForm: React.FC<Props> = React.memo((props) => {
        const Submit = (values: FilterType, {setSubmitting}: { setSubmitting: (isSubmitting: boolean) => void }) => {
            props.onFilterChanged(values)
            setSubmitting(false)
        }
        const filter = useSelector(getUserFilter)

        return (
            <div>
                <Formik
                    enableReinitialize={true}
                    initialValues={{term: filter.term, friend: filter.friend}}
                    validate={FormValidator}
                    onSubmit={Submit}
                >
                    {({isSubmitting}) => (
                        <Form className={s.formseaarch}>
                            <Field className={s.inoutsearch} type="text" name="term"/>
                            <Field className={s.selectInput} name="friend" as="select">
                                <option value="null">All</option>
                                <option value="true">Only Followed</option>
                                <option value="false">Only Unfollowed</option>
                            </Field>
                            <Button className={s.bi}>
                                <button className={s.searchbutton} type="submit" disabled={isSubmitting}>
                                    Submit
                                </button>
                            </Button>

                        </Form>
                    )}
                </Formik>
            </div>
        )
    }
)