import {ResultCodeEnum} from "../API/API";
import {UserType} from "../Types/Types";
import {actionsType, BaseThunkType} from "./redux-store";
import {usersAPI} from "../API/UsersAPI";


let initialState = {
    users: [] as Array<UserType>,
    pageSize: 10,
    totalUserConunt: 0,
    currentPage: 1,
    isFetcheng: false,
    isReloadButton: [] as Array<number>, //array of user id
    filter: {
        term: "",
        friend: null as null | boolean
    }

};

export type initialStateType = typeof initialState
export type FilterType = typeof initialState.filter


const usersReducer = (state = initialState, action: ActionsTypes): initialStateType => {
    switch (action.type) {
        case "SN/USER-REDUCER-FOLLOW": {
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.userID) {
                        return {...u, followed: true}
                    }
                    return u;
                }),
            }
        }
        case "SN/USER-REDUCER-UNFOLLOW": {
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.userID) {
                        return {...u, followed: false}
                    }
                    return u;
                }),
            }
        }
        case "SN/USER-REDUCER-SETUSERS": {
            return {
                ...state,
                users: [...action.users]
            }
        }
        case "SN/USER-REDUCER-SETCURENTPAGE": {
            return {
                ...state,
                currentPage: action.currentPage
            }
        }
        case "SN/USER-REDUCER-SETTOTALUSERCURENT": {
            return {
                ...state,
                totalUserConunt: action.count
            }
        }
        case "SN/USER-REDUCER-TOGGLEISFETCHING": {
            return {...state, isFetcheng: action.isFetcheng}
        }


        case  "SN/USER-REDUCER-RELOADBUTTON": {
            return {
                ...state,
                isReloadButton: action.isFetcheng
                    ? [...state.isReloadButton, action.userId]
                    : state.isReloadButton.filter(id => id != action.userId)
            }

        }

        case "SN/USER-REDUCER-FILTER": {
            return {...state, filter: action.payload}

        }
        default:
            return state;


    }


}


type ActionsTypes = actionsType<typeof actions>

export const actions = {
    Reload: (isFetcheng: boolean, userId: number) => {
        return {
            type: 'SN/USER-REDUCER-RELOADBUTTON',
            isFetcheng, userId
        } as const
    },


    toggleIsFenching: (isFetcheng: boolean) => {
        return {
            type: 'SN/USER-REDUCER-TOGGLEISFETCHING',
            isFetcheng,
        } as const
    },


    setTotalUserConunt: (totalUserConunt: number) => {
        return {
            type: 'SN/USER-REDUCER-SETTOTALUSERCURENT',
            count: totalUserConunt,
        } as const
    },


    setCurrentPage: (currentPage: number) => {
        return {
            type: 'SN/USER-REDUCER-SETCURENTPAGE',
            currentPage: currentPage
        } as const
    },

    setFilter: (filter: FilterType) => {
        return {
            type: 'SN/USER-REDUCER-FILTER',
            payload: filter
        } as const
    },


    follow: (userID: number) => {
        return {
            type: 'SN/USER-REDUCER-FOLLOW',
            userID: userID,
        } as const
    },


    unfollow: (userID: number) => {
        return {
            type: 'SN/USER-REDUCER-UNFOLLOW',
            userID: userID,
        } as const
    },


    setUser: (users: Array<UserType>) => {
        return {
            type: 'SN/USER-REDUCER-SETUSERS',
            users: users,
        } as const
    },
}

type ThunkType = BaseThunkType<ActionsTypes>


export const getUser = (currentPage: number, pageSize: number, filter: FilterType): ThunkType => {
    return async (dispatch, getState) => {
        dispatch(actions.toggleIsFenching(true))
        dispatch(actions.setCurrentPage(currentPage))
        dispatch(actions.setFilter(filter))


        let data = await usersAPI.getUser(currentPage, pageSize, filter.term, filter.friend)
        dispatch(actions.toggleIsFenching(false))
        dispatch(actions.setUser(data.items))
        dispatch(actions.setTotalUserConunt(data.totalCount))

    }
}

export const unfolowSuccess = (userId: number): ThunkType => {
    return async (dispatch) => {
        dispatch(actions.Reload(true, userId))
        let data = await usersAPI.userUnfollow(userId)
        if (data.resultCode === ResultCodeEnum.Success) {
            dispatch(actions.unfollow(userId))
        }

        dispatch(actions.Reload(false, userId))

    }
}

export const folowSuccess = (userId: number): ThunkType => {
    return async (dispatch) => {

        dispatch(actions.Reload(true, userId))
        let data = await usersAPI.userFollow(userId)

        if (data.resultCode === ResultCodeEnum.Success) {
            dispatch(actions.follow(userId))
        }
        dispatch(actions.Reload(false, userId))
    }
}

export default usersReducer;