import {NavLink} from 'react-router-dom';
import d from './StylleHeader/Header.module.css'
import React from "react";
import {Button, Col, Menu, Row} from "antd";
import {Header} from 'antd/lib/layout/layout';
import {useDispatch, useSelector} from "react-redux"
import {logout} from "../../Redux/auth-reducer";

import {selectorId, selectorIsAuth, selectorLogin} from "../../Redux/auth-selector";


export const HeaderApp: React.FC = () => {
    const isAuth = useSelector(selectorIsAuth)
    const login = useSelector(selectorLogin)
    const id = useSelector(selectorId)

    const dispatch = useDispatch()

    const logouts = () => {
        dispatch(logout())
    }
    let p;
    if (isAuth == false) {
        p = <Header className="header">
            <div className="logo"/>
            <Row>
                <Col span={22}>
                    <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['1']}>
                    </Menu>
                </Col>

                <Col span={2}>
                    {isAuth ? <div>{login} - <Button onClick={logouts}>Log out</Button></div> :
                        <NavLink to="/login"> <Button>Login</Button> </NavLink>}
                </Col>

            </Row>
        </Header>
    } else {
        p = <Header className="header">
            <div className="logo"/>
            <Row>
                <Col span={20}>
                    <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['1']}>
                        <Menu.Item key="1">Friend Fiender</Menu.Item>
                    </Menu>
                </Col>
                <Col span={2}>
                    <div className={d.name}>
                        {login}
                    </div>
                </Col>
                <Col span={2}>
                    <Button onClick={logouts}>Log out</Button>
                </Col>

            </Row>
        </Header>


    }


    return (
        <div>
            {p}
        </div>


    );
}