import React, {useState} from 'react'
import styles from "./StyleUsers/Users.module.css"
import cn from 'classnames'

type PropsType = {
    totalUserConunt: number
    pageSize: number
    currentPage?: number
    onPageChanged?: (pageNumber: number) => void
    portionSize?: number
}

let Pagination: React.FC<PropsType> = ({
                                           totalUserConunt, pageSize,
                                           currentPage = 1,
                                           onPageChanged = x => x,
                                           portionSize = 10
                                       }) => {

    let pagesCount = Math.ceil(totalUserConunt / pageSize);

    let pages: Array<number> = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);

    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionPageNumber = portionNumber * portionSize;


    return <div className={cn(styles.paginator)}>

        {pagesCount === 1 ?
            <button onClick={() => {
                let n
                if (currentPage > 1) {
                    n = currentPage % 10
                    if (+n === 1) {
                        setPortionNumber(portionNumber - 1)
                    }
                    onPageChanged(currentPage - 1)
                }
            }
            }>PREV
            </button>
            : ""
        }

        {pages
            .filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
            .map((p) => {
                return <span className={cn({
                    [styles.selectedPage]: currentPage === p
                }, styles.pageNumber)}
                             key={p}
                             onClick={(e) => {
                                 onPageChanged(p);
                             }}>{p}</span>
            })}


        {
            portionCount > portionNumber &&
            <button onClick={() => {
                if (Number.isInteger(currentPage / 10)) {
                    setPortionNumber(portionNumber + 1)
                }
                onPageChanged(currentPage + 1)

            }}>NEXT</button>
        }


    </div>
}

export default Pagination;
