import {getItemsType, instance, APIResponseType} from "./API";


export const usersAPI = {
    getUser(currentPage: number, pageSize: number, term: string = '', friend: null | boolean = null) {
        return instance.get<getItemsType>(`users?page=${currentPage}&count=${pageSize}&term=${term}` + (friend === null ? "" : `&friend=${friend}`),).then(response => {
            return response.data
        });
    },

    userUnfollow(id: number) {
        return instance.delete<APIResponseType>(`follow/` + id).then(response => {
            return response.data
        })
    },
    userFollow(id: number) {
        return instance.post<APIResponseType>('follow/' + id, {}).then(response => {
            return response.data
        })
    }
}