import {createSelector} from "reselect";
import {AppStateType} from "./redux-store";

export const getUsers = (state: AppStateType) => {
    return state.usersPage.users

}

export const getUsersSuper = createSelector(getUsers, (users) => {
    return users

})

export const getPageSize = (state: AppStateType) => {
    return state.usersPage.pageSize

}
export const getTotalUserConunt = (state: AppStateType) => {
    return state.usersPage.totalUserConunt

}
export const getCurrentPage = (state: AppStateType) => {
    return state.usersPage.currentPage

}
export const getIsFetcheng = (state: AppStateType) => {
    return state.usersPage.isFetcheng

}

export const getIsReloadButton = (state: AppStateType) => {
    return state.usersPage.isReloadButton

}

export const getUserFilter = (state: AppStateType) => {
    return state.usersPage.filter

}