import {ResultCodeEnum} from "../API/API";
import {stopSubmit} from "redux-form";
import noview from "../assets/img/no-view.svg";
import {actionsType, BaseThunkType} from "./redux-store";
import {authAPI} from "../API/AuthAPI";
import {securytiAPI} from "../API/SecurytiAPI";


let initialState = {
    id: null as number | null,
    login: null as string | null,
    email: null as string | null,
    isAuth: false as boolean | null,
    error: "" as string | null,
    watchstatus: "password" as string | null,
    icon_watch: noview as typeof noview,
    captchaUrl: null as string | null,
};

export type initialStateType = typeof initialState


const authReducer = (state = initialState, action: ActionsTypes): initialStateType => {
    switch (action.type) {
        case "SN/AUTH-REDUXER-SET_USER_DATA": {
            return {
                ...state,
                ...action.data,

            }

        }
        case "SN/AUTH-REDUXER-SET_WATCH_STATUS": {
            return {...state, watchstatus: action.watchstatus}
        }

        case "SN/AUTH-REDUXER-SET_ICON_STATUS": {
            return {...state, icon_watch: action.icon_watch}
        }

        case "SN/AUTH-REDUXER-GET_CAPTHCA_URL" : {

            return {
                ...state,
                ...action.payload,
            }
        }

        default:
            return state;


    }
}

type ActionsTypes = actionsType<typeof actions>

export const actions = {
    setUserData: (id: number | null, email: string | null, login: string | null, isAuth: boolean) => ({
        type: "SN/AUTH-REDUXER-SET_USER_DATA", data: {id, email, login, isAuth}
    } as const),
    getCaptcha: (captchaUrl: string) => ({type: "SN/AUTH-REDUXER-GET_CAPTHCA_URL", payload: {captchaUrl}} as const),
    watchStatus: (watchstatus: string) => {
        return {type: "SN/AUTH-REDUXER-SET_WATCH_STATUS", watchstatus,} as const
    },
    watchIconStatus: (icon_watch: string) => {
        return {type: "SN/AUTH-REDUXER-SET_ICON_STATUS", icon_watch,} as const
    }


}

type ThunkType = BaseThunkType<ActionsTypes | ReturnType<typeof stopSubmit>>

export const auth = (): ThunkType => {
    return async (dispatch) => {
        let response = await authAPI.getAuth()
        if (response.resultCode === ResultCodeEnum.Success) {
            let {id, login, email} = response.data
            dispatch(actions.setUserData(id, email, login, true));
        }
    }
}


export const login = (email: string, password: string, rememberme: boolean, captcha: string): ThunkType => {

    return async (dispatch) => {
        let response = await authAPI.login(email, password, rememberme, captcha)
        if (response.resultCode === ResultCodeEnum.Success) {
            dispatch(auth())
        } else {
            if (response.resultCode === ResultCodeEnum.CapthcaIsRequed) {
                dispatch(getCaptchUrl())
            }
            let message = response.messages.length > 0 ? response.messages[0] : "Some error";
            dispatch(stopSubmit("login", {_error: message}))

        }

    }
}


export const logout = (): ThunkType => {
    return async (dispatch) => {
        let response = await authAPI.logout()
        if (response.data.resultCode === ResultCodeEnum.Success) {
            dispatch(actions.setUserData(null, null, null, false))
        }
    }
}

export const getCaptchUrl = (): ThunkType => {
    return async (dispatch) => {
        const response = await securytiAPI.getCaptcha()
        let captchaUrl = response.url
        dispatch(actions.getCaptcha(captchaUrl))

    }
}


export default authReducer;